// 详情页
import React from 'react'
const Details = (props) => {
    const liStyle = {
        float: 'left',
        listStyle: 'none',
        fontSize: '12px',
        color: '#999',
        width: '24px',
        padding: '0 10px',
        textAlign: 'center',
        lineHeight: '20px'
    }
    const liStyle2 = {
        fontSize: '14px',
        color: '#fff',
        textAlign: 'center',
        lineHeight: '40px',
        width: '115px',
        height: '40px',
        padding: '0px',
        background: '#ffc500',
        borderTopLeftRadius: '20px',
        borderBottomLeftRadius: '20px',
    }
    return (
        <div>
            {/* 图片、返回 */}
            <img src='https://img.alicdn.com/imgextra/i3/40717165/TB2qcEBk_lYBeNjSszcXXbwhFXa_!!40717165.jpg_640x640q80_.webp'
                style={{
                    width: '100%'
                }} />
            <button onClick={() => props.history.goBack()}
                style={{
                    position: 'absolute',
                    top: '10px',
                    left: '10px',
                    borderRadius: '50%',
                    border: '0px',
                    width: '34px',
                    height: '34px',
                    background: '#00000066'
                }}
            >
                <i className='iconfont icon-zuojiantou' style={{ color: '#fff' }}></i>
            </button>
            {/* 底部按钮 */}
            <div style={{
                position: 'fixed',
                bottom: '0px',
                width: '100%',
                height: '35px',
                padding: '10px 0px',
                background: '#fff',
                zIndex: 111
            }}>
                <ul style={{ margin: 0, padding: 0 }}>
                    <li style={liStyle}><i style={{ color: '#ff5000', fontSize: '18px' }} className='iconfont icon-dianpu' />店铺</li>
                    <li style={liStyle}><i style={{ fontSize: '18px' }} className='iconfont icon-kefu' />客服</li>
                    <li style={liStyle}><i style={{ fontSize: '18px' }} className='iconfont icon-shoucang' />收藏</li>
                    <li style={{ ...liStyle, ...liStyle2 }}>加入购物车</li>
                    <li style={{
                        ...liStyle,
                        ...liStyle2,
                        borderRadius: 0,
                        borderTopRightRadius: '20px',
                        borderBottomRightRadius: '20px',
                        background: '#FE560A'
                    }}
                    >立即购买</li>
                </ul>
            </div>
        </div>
    )
}

export default Details;