// 首页
import React, { Component } from 'react'
import { Carousel, Grid } from 'antd-mobile';
import { Link } from 'react-router-dom';

class Home extends Component {
    // 轮播图
    state = {
        data: [ 
                'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
                'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
                'https://gw.alicdn.com/imgextra/i3/29/O1CN01wYa8xO1C5K7Ka9quz_!!29-0-lubanu.jpg', 
                'https://gw.alicdn.com/imgextra/i2/69/O1CN012kgqDf1CNdxWjfTPO_!!69-0-lubanu.jpg', 
                'https://gw.alicdn.com/imgextra/i3/56/O1CN01QKKUjx1CHgoKDEJx6_!!56-0-lubanu.jpg', 
                'https://gw.alicdn.com/imgextra/i1/62/O1CN011QHHZe1CKRBeSgsxz_!!62-0-lubanu.jpg', 
                'https://gw.alicdn.com/imgextra/i2/87/O1CN015w4ZpM1CVt5SJMwF8_!!87-0-lubanu.jpg',
                'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000005304/O1CN01lV1v0g1p3HMZaSxuP_!!6000000005304-0-octopus.jpg'
            ],
    }
    render() {
        // 商品 宫格
        return (
            <div>
                {/* 搜索框 */}
                <div style={{
                    width: '100%',
                    height: '37px',
                    background: 'rgb(255,133,42)',
                    position: 'fixed',
                    top: 0,
                    zIndex: 111
                }}>
                    <i className='iconfont icon-taobao' 
                        style={{float:'left', fontSize:'27px', color:'#fff',margin:'4px  0 0 7px'}}></i>
                    <Link to='/search'>
                        <div style={{
                            height: '25px',
                            margin: '6px 9px 6px 42px',
                            background: '#ff4e22',
                            borderRadius: '4px',
                            lineHeight: '25px',
                            textAlign: 'center',
                            color: '#f0e0dc',
                            fontSize: '13px'
                        }}
                            className='iconfont icon-xingtaiduICON_sousuo---copy'
                        >寻找宝贝店铺</div>
                    </Link>
                </div>
                {/* 轮播图 */}
                <Carousel
                    autoplay={true}
                    infinite
                    style={{ marginTop: '37px' }}
                >
                    {this.state.data.map(val => {
                        return <a key={val} href="#">
                            <img
                                src= {val}
                                alt=""
                                style={{ width: '100%', verticalAlign: 'top' }}
                                onLoad={() => {
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                    })}
                </Carousel>
                {/* 商品 宫格 */}
                <div style={{
                    padding: '6px',
                    marginBottom: '50px'
                }}>
                    <Grid
                        itemStyle={{ background: 'rgb(245,245,249)' }}
                        data={new Array(10)}
                        hasLine={false}
                        square={false}
                        columnNum={2}
                        renderItem={() => {
                            return <div style={{
                                width: '171px',
                                height: '260px',
                                background: '#fff',
                                borderRadius: '6px',
                                margin: '-9px auto'
                            }}>
                                <Link to='/details'>
                                    <img src='https://gw.alicdn.com/bao/uploaded/i1/40717165/TB22917sVXXXXX1XpXXXXXXXXXX_!!40717165.jpg_500x500q90.jpg_.webp'
                                        style={{
                                            width: '100%',
                                            borderTopLeftRadius: '6px',
                                            borderTopRightRadius: '6px'
                                        }}
                                    />
                                    <div style={{ height: '100px', padding: '6px 0 0 6px', textAlign: 'left' }}>
                                        <span style={{
                                            fontSize: '13px',
                                            display: 'inline-block',
                                            width: '146px',
                                            height: '37px',
                                            margin: '5px 0 0 5px',
                                            overflow: 'hidden',
                                            lineHeight: '19px',
                                            color: '#000'
                                        }}>清雅浪漫玉兰花浪漫梨花仿真花套装花艺客厅绢花装饰假花干花摆花</span>
                                        <p style={{ margin: '10px 0 0 5px' }}>
                                            <span style={{
                                                color: '#ff5500',
                                                fontSize: '12px'
                                            }}>￥</span>
                                            <span style={{
                                                color: '#ff5500',
                                                fontSize: '14px',
                                                margin: '0 4px 0 4px'
                                            }}>88.8</span>
                                            <span style={{
                                                color: '#999',
                                                fontSize: '12px'
                                            }}>48人已购买</span>
                                        </p>
                                    </div>
                                </Link>
                            </div>
                        }
                        }
                    />
                </div>
            </div>
        )
    }
}

export default Home;